import React, { Component } from 'react';
import './App.css';
import menu from './menu.png'
import fetchJsonp from 'fetch-jsonp'
import classnames from 'classnames';
import registerServiceWorker from './registerServiceWorker';
// import './service-worker.js'

var map;
let markers = [], infoWindows=[];
let istAPI=[];
// let locationCategory=[], list=[];

class App extends Component {
    constructor(props) {
      super(props);
      this.state = {
        locations :[
            {
              title: 'kungstradgarden',
              location: {lat: 59.3314807, lng: 18.0715013} ,
              category: 'Park',
              tag:'kungstradgarden'
            },
            {
              title: 'Berzelii Park',
              location: {lat: 59.3325803, lng: 18.0745944},
              category: 'Park',
              tag:'berzeliipark'
            },
            {
              title: 'The Royal Place',
              location: {lat:59.3268215, lng: 18.0717194} ,
              category: 'Entertaimment',
              tag:'royalplace'
            },
            {
              title: 'Royal Swedish Opera',
              location: {lat: 59.3297318, lng: 18.0704829} ,
              category: 'Entertaimment',
              tag:'kungligaoperan'
            },
            {
              title: 'Gallerian',
              location: {lat: 59.3312008, lng: 18.0673991} ,
              category: 'Shopping',
              tag:'galleria'
            },
            {
              title: 'Museum of Medieval Stockholm',
              location: {lat: 59.3282928, lng: 18.070308} ,
              category: 'Museum',
              tag:'museumofmedievalstockholm'
            }
    ],
      map:{},
      category:"All",
      tags:[],
      url:'',
      active: false
};
      this.initMap = this.initMap.bind(this);

    }



    componentDidMount() {
        window.initMap = this.initMap;
          loadMapJS('https://maps.googleapis.com/maps/api/js?key=AIzaSyCQv4i45Nms1mxMcqGFnfA9RTow2XHWTho&v=3&callback=initMap')

          {/* https://www.instagram.com/developer/endpoints/tags/#get_tags_media_recent
            https://www.npmjs.com/package/fetch-jsonp */}
          this.state.locations.map((location,index) => {
            return fetchJsonp(`https://api.instagram.com/v1/tags/${location.tag}/media/recent?access_token=7996642597.cba7db7.beeac3b6b6bf40ef91d5d26260dba293`)
            .then(response=> response.json()).then(responseJson => {
              istAPI.push(responseJson.data[0].images.standard_resolution.url)
              this.findTag(istAPI);
            }).catch(error => console.log(error))
          })
      }


      componentDidUpdate(){
        const {map,locations,tags} = this.state;
        let bounds = new window.google.maps.LatLngBounds();
        // let infowindow = new window.google.maps.InfoWindow();
        let locationCategory=locations;

        markers.forEach(mark=> mark.setMap(null));

        markers=[];
        infoWindows=[];

        if (this.state.category === "All"){
          locationCategory=locations;
        }
        else {
          locationCategory = locations.filter( m => m.category=== this.state.category);
          // console.log(locationCategory);
        }

        locationCategory.map((marker,index) => {

          let contentString = '<div id="images">' + marker.title + '<br><img alt='+marker.title+'name='+marker.tag+' src='+tags[index]+'></div>';

          let addInfo = new window.google.maps.InfoWindow({
            content :contentString,
          });

          let addM = new window.google.maps.Marker({
                map: map,
                position: marker.location,
                title: marker.title,
                tag: marker.tag,
                animation: window.google.maps.Animation.DROP
                });
          markers.push(addM);
          infoWindows.push(addInfo);

          addM.addListener('click', function() {
            infoWindows.forEach(info => {info.close()});
            {/* https://stackoverflow.com/questions/14657779/google-maps-bounce-animation-on-marker-for-a-limited-period*/}
            addM.setAnimation(window.google.maps.Animation.BOUNCE);
            setTimeout(addM.setAnimation(null), 2000);
            addInfo.open(map,addM);

          });
          markers.forEach(m => bounds.extend(m.position))
        });
        // console.log(this.state.category);
          }

          list = (item, event) => {
            let currentItem = markers.filter(current => current.title === item.title)
            window.google.maps.event.trigger(currentItem[0],'click');
          }

    initMap() {

    var getMap = document.getElementById('map');

    getMap.style.height = window.innerHeight + "px";
    map = new window.google.maps.Map(getMap, {
        center: {lat: 59.3293235, lng: 18.0685808},
        zoom: 15
    });
    this.setState({map:map});

//
//     if('serviceWorker' in navigator) {
//   navigator.serviceWorker.register('/registerServiceWorker', { scope: '/' })
//     .then(function(registration) {
//           console.log('Service Worker Registered');
//     });
//
//   navigator.serviceWorker.ready.then(function(registration) {
//      console.log('Service Worker Ready');
//   });
// }
}

    findByCategory = (event) => {
      this.setState({ category: event })
    }

    findTag = (t) => {

      this.setState({ tags:t.sort(), })
    }

    findURL = (t) => {
      let k= this.state.tags.filter(m=> { m.tag ===t.tag})
      // console.log(k);
      this.setState({ url:k.url, })
      return k.url;
    }

    click() {
      let a;
      if (this.state.active === true) {
        a = false;
      } else {
        a =true
      }
  		this.setState( {active: a});
  	}



    render() {
      const {map,locations} = this.state;
      {/* https://www.reddit.com/r/reactjs/comments/36cbhr/how_do_you_toggle_a_css_class_on_click_in_react/ */}
      let classes = classnames('menu-canvas', {'show-canvas': this.state.active});
      let locationCategory=locations;
      if (this.state.category === "All"){
        locationCategory=locations;
      }
      else {
        locationCategory = locations.filter( m => m.category=== this.state.category);
        // console.log(locationCategory);
      }
      return (
            <div>
  <header>
  <span tabIndex="1">Neighborhood Map</span>
  <div className="open">
    <button onClick={this.click.bind(this)} tabIndex="2"><img role="img" aria-label="menu button" src={menu} alt="menu button" width="40" height="40"/></button>
      </div>
    </header>
    <div className={"menu-canvas",classes} ref={this.textInput}>
      <div>
          <select id="list" tabIndex="3" onChange={event => this.findByCategory(event.target.value)}>
            <option value="All">All</option>
            <option value="Museum">Museum</option>
            <option value="Park">Park</option>
            <option value="Shopping">Shopping</option>
            <option value="Entertaimment">Entertaimment</option>
          </select>
      </div>
      <div className="mm">
        <ul className="listMarker" tabIndex="4">
        {locationCategory.map((location, index) =>
        <li tabIndex={index+5} name={index} key={index} onClick={this.list.bind(this,location)}>{location.title}</li>)}
        </ul>
      </div>
      </div>
                <div id="m" role="application" tabIndex="-1">
                <div id="map" role="region" aria-label="Stockholm Neighborhood"></div>
                </div>
            </div>
        );
    }
  }


export default App;

/**
 * Load the google maps Asynchronously
 * @param {url} url of the google maps script
 */
function loadMapJS(src) {
    var ref = window.document.getElementsByTagName("script")[0];
    var script = window.document.createElement("script");
    script.src = src;
    script.async = true;
    script.onerror = function () {
        document.write("Google Maps can't be loaded");
    };
    ref.parentNode.insertBefore(script, ref);
}



